
.close-icon {
  position: absolute;
  right: 0;
  margin: 10px 10px 0 0;
}
.side-menu {
  width: 200px;
  position: fixed;
  z-index: 100;
  left: 0px;
  top: 0;
  bottom: 0;
  background: radial-gradient(circle farthest-side at 0% 50%, #2b2b2b 23.5%, rgba(240, 166, 17, 0) 0) 21px 30px, radial-gradient(circle farthest-side at 0% 50%, #212121 24%, rgba(240, 166, 17, 0) 0) 19px 30px, linear-gradient(#2b2b2b 14%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 85%, #2b2b2b 0) 0 0, linear-gradient(150deg, #2b2b2b 24%, #212121 0, #212121 26%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 74%, #212121 0, #212121 76%, #2b2b2b 0) 0 0, linear-gradient(30deg, #2b2b2b 24%, #212121 0, #212121 26%, rgba(240, 166, 17, 0) 0, rgba(240, 166, 17, 0) 74%, #212121 0, #212121 76%, #2b2b2b 0) 0 0, linear-gradient(90deg, #212121 2%, #2b2b2b 0, #2b2b2b 98%, #212121 0%) 0 0 #2b2b2b;
  background-size: 40px 60px;
  color: white;
  box-shadow: 2px 2px 20px gray;
}
.side-menu p {
    font-weight: bold;
}
.side-menu .divider {
    border-top: 1px solid #555;
    margin: 10px;
}
.side-menu ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-left: 10px;
    text-align: left;
}
.side-menu li {
    padding: 5px 0;
    font-size: 14px;
    color: #aaa;
}
.side-menu li:hover {
      color: white;
      cursor: pointer;
}
.header {
  background: linear-gradient(to right, #3db881, #35495f);
  padding: 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header img {
    width: 30px;
    height: 30px;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.header:hover {
    cursor: pointer;
    text-decoration: underline;
}
.header:hover img {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
}



